import { useEffect, useState } from "react";
import {
  WalletsShow,
  addPaymentRequest,
  bankPublic,
  cloudImage,
  getPaymentMethodforFundRequest,
  trasferTo,
} from "../../../../api/login/Login";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";
import { baseUrlImage } from "../../../../baseUrl";
import { changeResellerTextToSponsor } from "../../../../utils/changesResellerTextToSponsor";
import { Modal, Button } from "react-bootstrap";
import Select from "react-select";
import "react-fancybox/lib/fancybox.css";

function AddPaymentRequest() {
  const [loader1, setloader1] = useState(false);
  const [paymentMethod, setPaymentMethod] = useState([]);
  const [bankData, setBankData] = useState([]);
  const [dataTrans, setDataTrans] = useState([]);
  const [profileImage, setProfileImage] = useState();
  const [imageShow, setImageShow] = useState();
  const [imageData, setImageData] = useState("");
  const [showImg, setShowImg] = useState(false);

  const [adminSelect, setAdminSelect] = useState(false);
  const [distributerSelect, setDistributerSelect] = useState(false);
  const [superDistributerSelect, setsuperDistributerSelect] = useState(false);

  const [errorMessage, setErrorMessage] = useState("");
  const [amountError, setAmountError] = useState("");

  const today = new Date().toISOString().split("T")[0];
  const maxLength = 125;
  const navigate = useNavigate();

  const [initialData, setInitialData] = useState({
    user_id: "",
    paymentDate: today,
    bankRef: "",
    amount: "",
    bank: "",
    method: "",
    account_number: "",
    receipt_img: "",
    remark: "",
    transferTo: "",
  });

  const [userSelect, setUserselect] = useState({ transferTo: "" });

  const amountLimits = {
    NEFT: { min: 2000, max: 500000 },
    RTGS: { min: 200000, max: 10000000 },
    IMPS: { min: 1, max: 500000 },
    BANK_TRANSFER: { min: 100, max: 2000000 },
  };

  const muiLikeInput = {
    height: "45px",
    borderRadius: "12px",
    backgroundColor: "var(--bs-primary3)",
    color: "var(--bs-primary2)",
    border: "1px solid var(--bs-primary2)",
    paddingLeft: "10px",
    fontSize: "14px",
    width: "100%",
  };

  const reactSelectStyles = {
    control: (provided) => ({
      ...provided,
      backgroundColor: "var(--bs-primary3)",
      color: "var(--bs-primary2)",
      borderColor: "var(--bs-primary2)",
      height: "45px",
      minHeight: "45px",
      borderRadius: "12px",
      fontSize: "14px",
      cursor: "pointer",
      boxShadow: "none",
    }),
    singleValue: (provided) => ({ ...provided, color: "var(--bs-primary2)" }),
    placeholder: (provided) => ({ ...provided, color: "var(--bs-primary2)" }),
    menu: (provided) => ({
      ...provided,
      backgroundColor: "var(--bs-primary3)",
    }),
    option: (provided, state) => ({
      ...provided,
      backgroundColor: state.isFocused ? "#99999950" : "var(--bs-primary3)",
      color: "var(--bs-primary2)",
      cursor: "pointer",
    }),
  };

  const handleChange = (e) => {
    const clone = { ...initialData };
    const value = e.target.value;
    const name = e.target.name;

    // remark limit
    if (name === "remark" && value.length > maxLength) {
      setErrorMessage(`Cannot type more than ${maxLength} characters`);
      return;
    } else setErrorMessage("");

    if (name === "amount") {
      const selectedMethod = initialData.method;
      if (selectedMethod && amountLimits[selectedMethod]) {
        const { min, max } = amountLimits[selectedMethod];
        if (parseFloat(value) < min || parseFloat(value) > max) {
          setAmountError(`Amount must be between ${min} to ${max}`);
        } else {
          setAmountError("");
        }
      }
    }

    clone[name] = value;

    if (name === "bank") {
      const selectedBank = bankData.find((item) => item.bank_name === value);
      clone.account_number = selectedBank?.bank_account_number || "";
    }

    setInitialData(clone);
  };

  const handleMethodChange = (selected) => {
    handleChange({ target: { name: "method", value: selected?.value } });
    setAmountError("");
  };

  const userSelectChange = (selected) => {
    setUserselect({ transferTo: selected.value });
    setAdminSelect(selected.value === "Admin");
    setDistributerSelect(selected.value === "distri");
    setsuperDistributerSelect(
      ["Super_dis", "zsm", "asm", "rsm", "reseller"].includes(selected.value)
    );
  };

  const fetchData = async () => {
    try {
      const resTransfer = await trasferTo();
      setDataTrans(resTransfer?.data?.data || []);

      const resBank = await bankPublic();
      setBankData(resBank?.data?.data || []);

      const resMethod = await getPaymentMethodforFundRequest();
      setPaymentMethod(resMethod?.data?.data?.activeTypes || []);
    } catch (error) {
      console.error(error);
    }
  };
  const uploadImage = async (e) => {
    if (!e.target.files || e.target.files.length === 0) {
      return alert("Please select a file");
    }

    const file = e.target.files[0];
    const allowedTypes = ["image/jpeg", "image/png", "image/gif"];
    if (!allowedTypes.includes(file.type)) {
      return alert("Only JPEG, PNG, GIF allowed");
    }

    const formData = new FormData();
    formData.append("image", file);

    try {
      const res = await cloudImage(formData);
      if (res?.data?.url) {
        const imageUrl = res.data.url;

        setImageShow(imageUrl);
        setProfileImage(imageUrl);

        console.log("Uploaded image URL:", imageUrl);
      } else {
        console.error("Unexpected upload response:", res.data);
      }
    } catch (error) {
      console.error("Image upload failed:", error);
      alert("Image upload failed");
    }
  };



  const submitData = async () => {

    if (!profileImage) {
      toast.error("Please upload slip first");
      return;
    }
    setloader1(true);

    const payload = {
      ...initialData,
      receipt_img: profileImage,
      transferTo: userSelect.transferTo,
      user_id: window.localStorage.getItem("userIdToken"),
    };

    try {
      const res = await addPaymentRequest(payload);
      if (res?.data?.statusCode === "200") {
        toast.success(res.data.message);
        setTimeout(() => {
          navigate(
            adminSelect
              ? "/payment-request-to-company"
              : "/payment-request-to-distributor"
          );
        }, 50);
      }
    } catch (err) {
      console.error(err);
    } finally {
      setloader1(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const isSubmitDisabled = () => {
    if (!userSelect.transferTo) return true;
    if (amountError) return true;

    if (adminSelect) {
      return (
        !initialData.remark ||
        !initialData.paymentDate ||
        !initialData.method ||
        !initialData.bank ||
        !initialData.account_number ||
        !initialData.amount ||
        !initialData.bankRef ||
        !imageShow
      );
    }

    return (
      (distributerSelect || superDistributerSelect) &&
      (!initialData.remark || !initialData.amount)
    );
  };

  const getSelectedOption = (options, value) =>
    options.find((opt) => opt.value === value) || null;

  return (
    <div className="PageHeading">
      <h1>ADD Fund Request</h1>
      <div className="ContentArea">
        <div
          className="card"
          style={{
            background: "var(--bs-primary1)",
            color: "var(--bs-primary2)",
            border: "1.5px solid var(--bs-primary2)",
            borderRadius: "16px",
            padding: "20px",
          }}
        >
          <div className="card-body">
            <form>
              <div
                className="form-row"
                style={{
                  border: "1px solid var(--bs-primary2)",
                  padding: "15px",
                  borderRadius: "16px",
                }}
              >
                {/* Transfer To */}
                <div className="form-group col-md-3">
                  <label>Select Transfer To *</label>
                  <Select
                    value={getSelectedOption(
                      dataTrans.map((item) => ({
                        value: item.value,
                        label: changeResellerTextToSponsor(item.title),
                      })),
                      userSelect.transferTo
                    )}
                    onChange={userSelectChange}
                    options={dataTrans.map((item) => ({
                      value: item.value,
                      label: changeResellerTextToSponsor(item.title),
                    }))}
                    styles={reactSelectStyles}
                    placeholder="Select Transfer"
                    isSearchable={false}
                  />
                </div>

                {adminSelect && (
                  <>
                    <div className="form-group col-md-3">
                      <label>Payment Date *</label>
                      <input
                        className="form-control"
                        type="date"
                        name="paymentDate"
                        value={initialData.paymentDate}
                        onChange={handleChange}
                        max={today}
                        style={muiLikeInput}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label>Select Method *</label>
                      <Select
                        value={getSelectedOption(
                          [
                            ...paymentMethod.map((m) => ({
                              value: m.type,
                              label: m.type,
                            })),
                            { value: "NEFT", label: "NEFT" },
                            { value: "RTGS", label: "RTGS" },
                            { value: "IMPS", label: "IMPS" },
                            { value: "BANK_TRANSFER", label: "Bank Transfer" },
                          ],
                          initialData.method
                        )}
                        onChange={handleMethodChange}
                        options={[
                          ...paymentMethod.map((m) => ({
                            value: m.type,
                            label: m.type,
                          })),
                          { value: "NEFT", label: "NEFT" },
                          { value: "RTGS", label: "RTGS" },
                          { value: "IMPS", label: "IMPS" },
                          { value: "BANK_TRANSFER", label: "Bank Transfer" },
                        ]}
                        styles={reactSelectStyles}
                        placeholder="Select Method"
                        isSearchable={false}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label>Bank Name *</label>
                      <Select
                        value={getSelectedOption(
                          bankData.map((b) => ({
                            value: b.bank_name,
                            label: b.bank_name,
                          })),
                          initialData.bank
                        )}
                        onChange={(selected) =>
                          handleChange({
                            target: { name: "bank", value: selected?.value },
                          })
                        }
                        options={bankData.map((b) => ({
                          value: b.bank_name,
                          label: b.bank_name,
                        }))}
                        styles={reactSelectStyles}
                        placeholder="Select Bank"
                        isSearchable={false}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label>Account Number *</label>
                      <input
                        className="form-control"
                        type="text"
                        name="account_number"
                        value={initialData.account_number}
                        disabled
                        style={muiLikeInput}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label>Amount *</label>
                      <input
                        className="form-control"
                        type="number"
                        name="amount"
                        value={initialData.amount}
                        onChange={handleChange}
                        style={muiLikeInput}
                      />
                      {amountError && (
                        <div className="error-message" style={{ color: "red" }}>
                          {amountError}
                        </div>
                      )}
                    </div>

                    <div className="form-group col-md-3">
                      <label>Bank Reference No *</label>
                      <input
                        className="form-control"
                        type="text"
                        name="bankRef"
                        value={initialData.bankRef}
                        onChange={handleChange}
                        style={muiLikeInput}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label>Remark *</label>
                      <textarea
                        className="form-control"
                        name="remark"
                        value={initialData.remark}
                        onChange={handleChange}
                        style={muiLikeInput}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label>Slip *</label>
                      <input
                        className="form-control"
                        type="file"
                        name="receipt_img"
                        onChange={uploadImage}
                        style={muiLikeInput}
                      />
                    </div>

                    {imageShow && (
                      <div className="form-group col-md-3">
                        <img
                          src={imageShow}
                          width={50}
                          onClick={() => setShowImg(true)}
                          style={{ cursor: "pointer", borderRadius: "8px" }}
                          alt="Slip"
                        />
                      </div>
                    )}

                  </>
                )}

                {(distributerSelect || superDistributerSelect) && (
                  <>
                    <div className="form-group col-md-3">
                      <label>Amount *</label>
                      <input
                        className="form-control"
                        type="number"
                        name="amount"
                        value={initialData.amount}
                        onChange={handleChange}
                        style={muiLikeInput}
                      />
                      {amountError && (
                        <div className="error-message" style={{ color: "red" }}>
                          {amountError}
                        </div>
                      )}
                    </div>

                    <div className="form-group col-md-3">
                      <label>Remark *</label>
                      <textarea
                        className="form-control"
                        name="remark"
                        value={initialData.remark}
                        onChange={handleChange}
                        style={muiLikeInput}
                      />
                    </div>
                  </>
                )}

                <div className="form-group col-md-12 text-center mt-3">
                  <button
                    type="button"
                    className={`btn ${isSubmitDisabled()
                        ? "commonbotton_disable"
                        : "btn-primary"
                      }`}
                    disabled={isSubmitDisabled() || loader1}
                    onClick={submitData}
                    style={{
                      backgroundColor: "#E8DAD6",
                      borderColor: "#E8DAD6",
                      color: "#000",
                    }}
                  >
                    {loader1 ? "Submitting..." : "Submit"}
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>

      {/* IMAGE MODAL */}
      <Modal show={showImg} onHide={() => setShowImg(false)} size="md">
        <Modal.Header closeButton></Modal.Header>
        <Modal.Body>
          <img src={imageShow} width="100%" alt="Slip Preview" />
        </Modal.Body>

        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowImg(false)}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default AddPaymentRequest;
